import React, { Component } from 'react'

const scripts = {
  google: 'https://www.gstatic.com/charts/loader.js'
}

class Travel extends Component {
  constructor (props) {
    super(props)

    this.state = {
      google: false
    }

    this.map = null
  }

  componentDidMount() {
    this.loadScript('google')
  }

  componentDidUpdate () {
    if (this.state.google) {
      this.initMap()
    }
  }

  loadScript = (name) => {
    if (document.querySelector('script[src="' + scripts[name] + '"]')) {
      this.setState({ [name]: true })
      return
    }

    const script = document.createElement('script')
    script.src = scripts[name]
    script.onload = () => {
      const next = Object.keys(scripts)[Object.keys(scripts).indexOf(name) + 1]
      this.setState({ [name]: true })
      if (next) {
        this.loadScript(next)
      }
    }
    document.body.appendChild(script)
  }

  initMap = () => {
    window.google.charts.load('current', {
      'packages':['geochart'],
      'mapsApiKey': 'AIzaSyD-9tSrke72PouQMnMX-a7eZSW0jkFMBWY'
    })
    window.google.charts.setOnLoadCallback(this.drawMap);

    console.log(window.google)
  }

  drawMap = () => {
    const data = window.google.visualization.arrayToDataTable([
      ['Country', 'Latitude'],
      ['Switzerland', 100],
      ['New Zealand', 80],
      ['Indonesia', 50],
      ['Germany', 50],
      ['Canada', 10],
      ['Japan', -100],
      ['United States', 10]
    ])

    const options = {
      colorAxis: {colors: ['#e31b23', '#fff', '#00853f']},
      backgroundColor: '#81d4fa'
    }

    this.map = new window.google.visualization.GeoChart(document.getElementById('geochart-colors'))
    this.map.draw(data, options)
  }

  render() {
    return (
      <section className='section' id='travel'>
        <div className='container'>
          <div className='h4 text-center mb-4 title'>My Travels</div>
          <div
            className='card'
            data-aos='fade-up'
            data-aos-anchor-placement='top-bottom'
          >
            <div className='card-body'>
              <div className='row'>
                <div className='col-12'>
                  <div id='geochart-colors'></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    )
  }
}

export default Travel
